import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Header from "../component/Header";
import Footer from "../component/Footer";
import { Row, Col, BackTop, Breadcrumb, Pagination, Skeleton } from 'antd';
import axios from 'axios';
import { token, news_api } from '../api/api.js';
// import kvimg from "../images/kv.jpg";
import kvimg from "../images/news_bg.jpg";

export default function News() {
    const num = 5; //一頁幾條新聞
    const [total, setTotal] = useState(0); //總新聞數    
    const [current, setCurrent] = useState(1); //當前頁面
    const [newslist, setNewslist] = useState(null); //所有新聞物件
    const [min, setMin] = useState(0); //頁數的最小值
    const [max, setMax] = useState(5); //頁數的最大值
    const onChangePage = (page) =>{
        setCurrent(page);
        if(page <= 1){
            setMin(0);
            setMax(num);
        }else{
            setMin((page-1)*num);
            setMax((page-1)*num+num);
        }
        document.getElementById('pagetitle').scrollIntoView({block: 'start', behavior: 'smooth'});      
    }
    //去除字串中的html標籤並只留下50字
    const delHtmlTag = (str) => {
        str = str.replace(/<[^>]+>/g,"");
        if(str.length > 50){
            str = str.substring(0,50);
        }
        return str;
    }
    const loadItem = () => {   
        let arr = [1,2,3,4,5];
        return arr.map((item,index) => {
            return <div className='news-item' key={index}>
                        <Skeleton active avatar />
                    </div>
        });
        
    };
    useEffect(()=>{
        axios({
            method: 'GET',
            url: news_api,
            responseType: 'json', 
            headers: {
                  Authorization: `Bearer ${token}` 
            }
        }).then(function (response) { // 成功後要做的事            
            setTotal(response.data.length); 
            setNewslist(response.data); 
        }).catch(function (error) { // 失敗後要做的事
            console.log(error);
        })
    },[])
    return (
        <div>
            <Header/>
            <div className='page-title-bg' style={{backgroundImage:`url(${kvimg})`}} id='pagetitle'>
                <p className='page-title-sub'>最新消息</p>  
                <h1>NEWS</h1>                
            </div>    
            <div className='breadcrumb'>
                <div className='container'>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            News
                        </Breadcrumb.Item>
                    </Breadcrumb>
                </div>   
            </div>   
            <section className='page-news-section'>
                <div className='container'>
                    <div className="page-news-content">
                        <Row>
                            <Col lg={{span:16,offset:4}} md={{span:16,offset:4}} sm={24} xs={24}>
                                <div className='page-news-list'>
                                    {/* <Link className='news-item' to="/news2/test1">
                                        <div className='news-item-date'>
                                            <div className='news-item-date-day'>26</div>
                                            <div className='news-item-date-month'>2022.07</div>
                                        </div>
                                        <div className='news-item-content'>
                                            <h3>漁電共生共同升壓站動土 首例民間投資明年完工</h3>
                                            <p>為了要加快台灣綠能發展，台南(25日)舉行首座「漁電共生共同升壓站」動土典禮，而這也是政府開放民間，投資設立共同升壓站的首例</p>
                                        </div>
                                    </Link>
                                    <Link className='news-item' to="/news2/test3">
                                        <div className='news-item-date'>
                                            <div className='news-item-date-day'>08</div>
                                            <div className='news-item-date-month'>2022.04</div>
                                        </div>
                                        <div className='news-item-content'>
                                            <h3>讓綠能更有價值！微電能源用太陽能推動共好價值鏈微電能源用太陽能推動共好價值鏈</h3>
                                            <p>全球追求淨零碳排如火如荼，台灣也宣布2050淨零轉型政策，積極發展再生能源，成為未來的發電主力，其中風電和光電更是重中之重。2015年創立的微電能源，跨界集結能量，持續致力加值綠能，讓太陽能不只是發電...</p>
                                        </div>
                                    </Link>
                                    <Link className='news-item' to="/news2/test2">
                                        <div className='news-item-date'>
                                            <div className='news-item-date-day'>08</div>
                                            <div className='news-item-date-month'>2022.04</div>
                                        </div>
                                        <div className='news-item-content'>
                                            <h3>太陽能的共好價值鏈？打造永續生活的可能</h3>
                                            <p>今天我們將一起來聊聊微電能源創立的故事、以及如何打造共好價值鏈</p>
                                        </div>
                                    </Link> */}
                                    {newslist ? newslist.slice(min,max).map(item => {
                                        if(item.external === "Y"){
                                            return  <a className='news-item' href={item.hyperlink} key={item.vid} target="_blank" rel="noreferrer">
                                                        <div className='news-item-date'>
                                                            <div className='news-item-date-day'>{item.dt.split("-")[2].slice(0,2)}</div>
                                                            <div className='news-item-date-month'>{item.dt.split("-")[0]+ "." + item.dt.split("-")[1]}</div>
                                                        </div>
                                                        <div className='news-item-content'>
                                                            <h3>{item.title}</h3>
                                                            <p>{delHtmlTag(item.content)}</p>
                                                        </div>
                                                    </a>
                                        }else{
                                            return  <Link className='news-item' to={`/news/${item.vid}`} key={item.vid}>
                                                        <div className='news-item-date'>
                                                            <div className='news-item-date-day'>{item.dt.split("-")[2].slice(0,2)}</div>
                                                            <div className='news-item-date-month'>{item.dt.split("-")[0]+ "." + item.dt.split("-")[1]}</div>
                                                        </div>
                                                        <div className='news-item-content'>
                                                            <h3>{item.title}</h3>
                                                            <p>{delHtmlTag(item.content)}</p>
                                                        </div>
                                                    </Link>
                                        }
                                       
                                    }): loadItem()}
                                </div>
                            </Col>
                        </Row>
                        <Row justify="center">
                            <Col>
                                <div className="pagination">
                                    <Pagination current={current} onChange={onChangePage} defaultCurrent={1} total={total} defaultPageSize={num} />
                                </div>
                            </Col>
                        </Row>
                    </div>
                    
                </div>
            </section>
            <BackTop />
            <Footer/>
        </div>
        
    );
}