import React, { useState, useRef, useEffect } from 'react';
import Header from "../component/Header";
import Footer from "../component/Footer";
import { Link } from "react-router-dom";
import { Row, Col, BackTop } from 'antd';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useTranslation } from 'react-i18next';
import kvimg from "../images/kv.jpg";
import aboutbg from "../images/about_bg.jpg";
import servicebg from "../images/service_bg.jpg";
import icon_solar from "../images/icon-solar.svg";
import icon_solar_yellow from "../images/icon-solar-yellow.svg";
import icon_water_yellow from "../images/icon-water-yellow.svg";
import icon_assets_yellow from "../images/icon-assets-yellow.svg";
import icon_leaves from "../images/icon-leaves.svg";
import icon_plug from "../images/icon-plug.svg";
import icon_light from "../images/icon-light.svg";
import icon_circle from "../images/icon-circle.svg";
import icon_sale from "../images/icon-sale.svg";
import advantage_1 from "../images/advantage_1.jpg";
import advantage_2 from "../images/advantage_2.jpg";
import advantage_3 from "../images/advantage_3.jpg";
import news1 from "../images/index_news_1.jpg";
import news2 from "../images/index_news_2.jpg";

export default function Home () {
  const { t } = useTranslation();
  gsap.registerPlugin(ScrollTrigger);
  const scrollref = useRef(null);
  const [btnClass, setBtnClass] = useState("");
  const toLink = (url) =>{
    let anchorElement = document.getElementById(url);
    anchorElement.scrollIntoView({block: 'start', behavior: 'smooth'});
  }

  
  useEffect(()=>{        
    setBtnClass("isview");
    const element = scrollref.current;

    gsap.from(element.querySelectorAll(".kv-title-word"),
    {
      y: 300, 
      duration: 1,
      stagger: 0.05
    });
    gsap.from(element.querySelector(".kv-subtitle"),
    {
      y: 300, 
      duration: 1.5,
      delay: 0.5,
    });
    gsap.fromTo(
      element.querySelector(".kv-img"),
      {        
        y: 0
      },
      {        
        y: 80,
        scrollTrigger: {
          trigger: element.querySelector(".kv-img"),
          start: "top top",
          end: "bottom center",
          scrub: true,
        }
      }
    );
    gsap.fromTo(
      element.querySelector(".about-bg"),
      {        
        y: 0
      },
      {        
        y: 80,
        scrollTrigger: {
          trigger: element.querySelector(".about-section"),
          start: "top top",
          end: "bottom center",
          scrub: true,
        }
      }
    );
    gsap.fromTo(
      element.querySelector(".about-section-title"),
      { 
        y: 300,
      },
      {
        y: 0,
        duration: 1,
        scrollTrigger: {
          trigger: element.querySelector(".about-section"),
          start: "top bottom",
          toggleClass: {targets: element.querySelector(".btn-more2"), className: "isview"},
        }
      }
    );       
    gsap.fromTo(
      element.querySelector(".news-section-title"),
      { y: 300 },
      {
        y: 0,
        duration: 1,
        scrollTrigger: {
          trigger: element.querySelector(".news-section"),
          start: "top bottom",
          toggleClass: {targets: element.querySelector(".btn-more3"), className: "isview"},
        }
      }
    );
    gsap.fromTo(
      element.querySelector(".service-section-title"),
      { y: 300 },
      {
        y: 0,
        duration: 1,
        scrollTrigger: {
          trigger: element.querySelector(".service-section-title"),
          start: "top bottom",
          toggleClass: {targets: element.querySelector(".btn-more4"), className: "isview"},
        }
      }
    );
    gsap.fromTo(
      element.querySelector(".service-bg"),
      {        
        y: 0
      },
      {        
        y: 120,
        scrollTrigger: {
          trigger: element.querySelector(".service-section"),
          start: "top top",
          scrub: true,
        }
      }
    );
    gsap.fromTo(
      element.querySelector(".advantage-section-title"),
      { y: 300 },
      {
        y: 0,
        duration: 1,
        scrollTrigger: {
          trigger: element.querySelector(".advantage-section"),
          start: "top 80%",
         
        }
      }
    );
    
    gsap.fromTo(
      element.querySelector(".service-section-move"),
      { y: 0 },
      {
        y: 0,
        duration: 1,
        ease: 'none',
        scrollTrigger: {
          trigger: element.querySelector(".service-container"),
          toggleClass: {targets: element.querySelector(".service-section-move"), className: "fixed"},
          start: `top 0%+=160px`,
          end: "bottom 0%",
          scrub: true,
        }
      }
    );
  
  },[])
  return (
  <div ref={scrollref}>   
    <Header transparent='true'/>
    <section className="kv">
      <img src={kvimg} className="kv-img" alt="kv"/>
      <div className='kv-title-wrap'>      
        <h1 className="kv-title">
          <span className='kv-title-word'>S</span>
          <span className='kv-title-word'>O</span>
          <span className='kv-title-word'>L</span>
          <span className='kv-title-word'>A</span>
          <span className='kv-title-word'>R</span>
          <br/>          
          <span className='kv-title-word'>AS</span>          
          <span className='kv-title-word'>A</span>      
          <span className='kv-title-word'>S</span>
          <span className='kv-title-word'>E</span>
          <span className='kv-title-word'>R</span>
          <span className='kv-title-word'>V</span>
          <span className='kv-title-word'>I</span>
          <span className='kv-title-word'>C</span>
          <span className='kv-title-word'>E</span>
        </h1>
        <h2 className="kv-subtitle">太陽能即服務</h2>
      </div>
      <a className={`btn-more secondary ${btnClass}`} href='/#' onClick={(e)=> {e.preventDefault();toLink("about");}}>
        <div className='btn-more-wrap'>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
            <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
            <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
          </svg>
          <i className='arrow-right'></i>
        </div>        
        <p className='bottom'>Scroll down</p>
      </a>
    </section>   
    <section className='about-section' id="about">
      <div className='container'>
        <Row>
          <Col lg={6} md={6} sm={24} xs={24}>
            <div className='section-title-wrap'>
              <div className='about-section-title'>
                <h2 className='section-title'>About Us</h2>
                <span>關於我們</span>
              </div>              
            </div>
            <Link className="btn-more btn-more2" to='/about'>
              <div className="btn-more-wrap">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
                  <circle r="38" cx="40" cy="40" stroke="#0d0d0d" strokeWidth="2" fill="none"></circle>
                  <circle r="38" cx="40" cy="40" stroke="#0d0d0d" strokeWidth="2" fill="none"></circle>
                </svg>
                <i></i>
              </div>        
              <p className='right'>See About Us</p>
            </Link>
          </Col>
          <Col lg={{span:12,offset:6}} md={{span:12,offset:6}} sm={24} xs={24}>
            <div className='first-section-p'>
              <p>{t('index.aboutp')}</p>
              <p>{t('index.aboutp2')}</p>
            </div>
            <div className='showNum-wrap'>
              <Row>
                <Col lg={8} md={8} sm={8} xs={8}>                  
                    <div className='showNum'>
                      <div className='showNum-icon'>                        
                        <img src={icon_solar_yellow} alt={icon_solar_yellow} />
                      </div>                    
                      <div className='showNum-title'><span>+720</span> MW</div>
                      <div className='showNum-p'>開發規模</div>
                    </div>                  
                </Col>
                <Col lg={8} md={8} sm={8} xs={8}>                  
                    <div className='showNum'>
                      <div className='showNum-icon'>
                        <img src={icon_water_yellow} alt={icon_water_yellow} />
                      </div>                    
                      <div className='showNum-title'><span>+160</span> MW</div>
                      <div className='showNum-p'>漁電共生規模</div>
                    </div>                  
                </Col>
                <Col lg={8} md={8} sm={8} xs={8}>                  
                    <div className='showNum'>
                      <div className='showNum-icon'>
                        <img src={icon_assets_yellow} alt={icon_assets_yellow} />
                      </div>                    
                      <div className='showNum-title'><span>+300</span> MW</div>
                      <div className='showNum-p'>共同升壓站規模</div>
                    </div>                  
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </div>
      <img className="about-bg" src={aboutbg} alt="aboutbg"/>
    </section>
    <section className='news-section'>
      <div className='section-top-line'></div>
      <div className='container'>        
        <Row>
          <Col lg={6} md={6} sm={24} xs={24}>
            <div className='section-title-wrap'>
              <div className='news-section-title'>
                <h2 className='section-title'>News</h2>
                <span>最新消息</span>  
              </div>               
            </div>  
            <Link className="btn-more btn-more3" to='/news'>
              <div className="btn-more-wrap">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
                  <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                  <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                </svg>
                <i></i>
              </div>        
              <p className='right'>See News</p>
            </Link>
          </Col>
          <Col lg={{span:12,offset:6}} md={{span:12,offset:6}} sm={24} xs={24}>
            <Link className='news-item' to="/news2/test1">
              <div className='news-item-date'>
                <div className='news-item-date-day'>26</div>
                <div className='news-item-date-month'>2022.07</div>
              </div>
              <div className='news-item-content with-photo'>
                  <h3>漁電共生共同升壓站動土 首例民間投資明年完工</h3>
                  <p>為了要加快台灣綠能發展，台南(25日)舉行首座「漁電共生共同升壓站」動土典禮，而這也是政府開放民間，投資設立共同升壓站的首例</p>
              </div>
              <div className='news-item-photo'>
                <img src={news1} alt="" />
              </div>
            </Link>            
            <Link className='news-item' to="/news2/test3">
              <div className='news-item-date'>
                <div className='news-item-date-day'>08</div>
                <div className='news-item-date-month'>2022.04</div>
              </div>
              <div className='news-item-content with-photo'>
                  <h3>讓綠能更有價值！微電能源用太陽能推動共好價值鏈微電能源用太陽能推動共好價值鏈</h3>
                  <p>全球追求淨零碳排如火如荼，台灣也宣布2050淨零轉型政策，積極發展再生能源，成為未來的發電主力，其中風電和光電更是重中之重。2015年創立的微電能源，跨界集結能量，持續致力加值綠能，讓太陽能不只是發電...</p>
              </div>
              <div className='news-item-photo'>
                <img src="https://storage.googleapis.com/www-cw-com-tw/article/202203/article-624568c7b0f76.jpg" alt="" />
              </div>
            </Link>
            <Link className='news-item' to="/news2/test2">
              <div className='news-item-date'>
                <div className='news-item-date-day'>08</div>
                <div className='news-item-date-month'>2022.04</div>
              </div>
              <div className='news-item-content with-photo' id='abc'>
                  <h3>太陽能的共好價值鏈？打造永續生活的可能</h3>
                  <p>今天我們將一起來聊聊微電能源創立的故事、以及如何打造共好價值鏈</p>
              </div>
              <div className='news-item-photo'>
                <img src={news2} alt="" />
              </div>
            </Link>
          </Col>
        </Row>
      </div>
    </section>
    <section className="service-section">
      <div className='container service-container'>
        <Row align="top">
          <Col lg={6} md={6} sm={24} xs={24}>
            <div className='service-section-move'>
              <div className='section-title-wrap'>
                <div className='service-section-title'>
                  <h2 className='section-title'>Our<br/>Service</h2>
                  <span>解決方案</span>
                </div>              
              </div>                       
              <Link className="btn-more white btn-more4" to='/service'>
                <div className="btn-more-wrap">
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
                    <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                    <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                  </svg>
                  <i></i>
                </div>        
                <p className='right'>See Our Service</p>
              </Link>
            </div>
          </Col>
          <Col lg={{span:12,offset:6}} md={{span:12,offset:6}} sm={24} xs={24}>
            <Row gutter={[40, 40]}>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_solar} alt={icon_solar} /></div>
                  </div>
                  <h5>太陽能電廠整合方案</h5>
                  <p>以專業團隊統籌執行電廠開發建置維運，微電持續開發太陽能與多場景的整合技術，從地面型、屋頂型，到農電共生、漁電共生，停車場、大型建築物的太陽能電廠整合開發，並以能源數據資訊的核心能力，未來應用為發展願景，推動跨界軟體與硬體的共同研發項目，創造多元的未來應用。</p>
                </div>
              </Col>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_leaves} alt={icon_leaves} /></div>
                  </div>
                  <h5>綠能應用解決方案</h5>
                  <p>以完整的電廠解決方案與跨領域團隊的專業，客製化綠能資產的投入與管理服務。對綠能資產投資、長期持有等不同的客戶需求，客製化設計方案；兼顧穩定性、持續性以及管理承諾，創造多方共贏的合作模式。</p>
                </div>
              </Col>
            </Row>
            <Row gutter={[40, 40]}>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_circle} alt={icon_circle} /></div>
                  </div>
                  <h5>綠能採購與再生能源憑證 (T-REC)</h5>
                  <p>微電能源以轉供、建置自發自用電廠開發等方式，結合綠能資產的規劃，滿足用電大戶、企業RE策略、用電確保與成本控制，以及再生能源憑證(T-REC)取得的策略需要。</p>
                </div>
              </Col>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_plug} alt={icon_plug} /></div>
                  </div>
                  <h5>綠電銷售代理</h5>
                  <p>微電能源以能源數據核心，開發售電服務，針對發電端與負載端進行分析，提供太陽能電廠與綠電用戶從預估、配對、轉供等綠電優化服務，並整合銀行、金融等專業解決方案，優化風險控管、收益可控的機制，協助太陽能電廠透過綠電銷售提升整體報酬效益。</p>
                </div>
              </Col>
            </Row>
            <Row gutter={[40, 40]}>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_light} alt={icon_light} /></div>
                  </div>
                  <h5>Light Donate:ESG/CSR應用</h5>
                  <p>微電能源使參與綠能的企業可將太陽能投資的報酬轉為公益價值，讓投資方在投資太陽能獲得ESG Investing中「環境Environmental」的表現之外，同時提升ESG Investing裡「社會Social」的價值。</p>
                </div>
              </Col>
              <Col lg={12} md={12} sm={12} xs={24}>
                <div className='service-item'>
                  <div className="service-icon">
                    <div className='icon-wrap'><img src={icon_sale} alt={icon_sale} /></div>
                  </div>
                  <h5>電廠銷售服務</h5>
                  <p>微電能源運用長期累積的電廠營運經驗及跨領域夥伴的網路，協助電廠引資銷售，透過評估、健檢、評價與交易設計，優化與實現電廠的交易價值。</p>
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
      <img className="service-bg" src={servicebg} alt="servicebg"/>
    </section>
    <section className='advantage-section'>
      <div className='container'>        
        <Row>
          <Col lg={24} md={24} sm={24} xs={24}>
            <div className='section-title-wrap'>
              <div className='advantage-section-title'>
                <h2 className='section-title'>Advantage</h2>
                <span>我們的優勢</span>
              </div>                   
            </div>            
          </Col>
        </Row>
        <Row align="stretch">
          <Col lg={24} md={24} sm={24} xs={24}>
              <Row gutter={32}>
                <Col lg={8} md={8} sm={24} xs={24}>
                  <div className='box box-flex-space-between'>
                      <div className='box-content'>
                        <h3>跨領域專業團隊</h3>
                        <p>集結科技、資訊、金融及太陽能開發、建置、營運、技術等各領域菁英，共同打造多元深度產業架構與運營模式。</p>
                      </div>
                      <div className='box-photo'>
                        <img src={advantage_1} alt="跨領域專業團隊" className='advantage-1'/>
                      </div>
                  </div>
                </Col>
                <Col lg={8} md={8} sm={24} xs={24}>
                  <div className='box box-flex-space-between'>
                      <div className='box-photo'>
                        <img src={advantage_2} alt="深厚專業知識和領航技術" className='advantage-2'/>
                      </div>
                      <div className='box-content'>
                        <h3>深厚專業知識和領航技術</h3>
                        <p>微電具備深厚的產業知識，執行力與軟硬整合實力，提供完整EPC服務，共同投資及建置營運各型態太陽能電廠，並應用在綠能資產、能源數據應用、農漁共生、社會企業與捐贈，到能源物聯網的研發合作，串聯能源多元場景。</p>
                      </div>
                      
                  </div>
                </Col>
                <Col lg={8} md={8} sm={24} xs={24}>
                  <div className='box box-flex-space-between'>
                      <div className='box-content'>
                        <h3>洞見趨勢的技術與服務</h3>
                        <p>以全球化的視野和宏觀政策角度為基礎，串聯核心與資訊技術，將「電流、金流、資訊流」匯聚成源源不絕的動力，提供彈性、多元的解決方案。</p>
                      </div>
                      <div className='box-photo'>
                        <img src={advantage_3} alt="洞見趨勢的技術與服務" className='advantage-3'/>
                      </div>
                  </div>
                </Col>
              </Row>            
          </Col>
        </Row>

      </div>
    </section>
    <BackTop />
    <Footer/>    
  </div>
  )  
};
