import React, { useState, useLayoutEffect, useEffect } from 'react';
import classNames from 'classnames';
import {NavLink, Link} from 'react-router-dom';
import Dropdown from './Dropdown';
import { Row, Col, Button } from 'antd';
import logo from "../images/logo.svg";
import logo_w from "../images/logo_white.svg";

const Header =  React.forwardRef((props,ref) => {  
    const [dropclass,setDropclass]=useState("");
    const [scroll,setScroll]=useState("");
    const [menuVisible, setMenuVisible] = useState(false);
    const headerClassName = classNames({
        clearfix: true,
        headerTranparent: props.transparent,
    });
    const menuDropItem_about = [
        {
            id:"公司介紹",
            title:"公司介紹",
            url:"/about",
            hash:"",
        },
        {
            id:"願景宗旨",
            title:"願景宗旨",
            url:"/about",
            hash:"view",
        },
        {
            id:"營運團隊",
            title:"營運團隊",
            url:"/about",
            hash:"team",
        },
        {
            id:"品牌精神",
            title:"品牌精神",
            url:"/about",
            hash:"spirits",
        },
        {
            id:"微電大事紀",
            title:"微電大事紀",
            url:"/about",
            hash:"history",
        },
    ]
    const menuDropItem_service = [
        {
            id:"太陽能電廠整合方案",
            title:"太陽能電廠整合方案",
            url:"/service",
            hash:"",
        },
        {
            id:"綠能應用解決方案",
            title:"綠能應用解決方案",
            url:"/service",
            hash:"green",
        },
        {
            id:"電廠銷售服務",
            title:"電廠銷售服務",
            url:"/service",
            hash:"green",
        },
        {
            id:"綠能採購",
            title:"綠能採購",
            url:"/service",
            hash:"wali",
        },
        {
            id:"綠電銷售代理",
            title:"綠電銷售代理",
            url:"/service",
            hash:"wali",
        },
        {
            id:"Light Donate",
            title:"Light Donate來點亮",
            url:"/service",
            hash:"lightdonate",
        },        
    ]
    const dropItemSet = (isview) => {
        setDropclass(isview);
    }
    useLayoutEffect(()=>{
        window.scrollTo(0, 0);
    },[])
    useEffect(()=>{
        if(props.transparent === "true"){
            window.addEventListener('scroll',()=>{      
                setScroll(window.scrollY > 0 ? "scroll" : "");
            });
        }        
    })
    const phoneClick = () => {
        setMenuVisible(!menuVisible);
    };

    const menu = [
        <ul className="header-menu" key={"menu"}>
            <li>
                <NavLink to="/">
                    首頁
                </NavLink>
            </li>            
            <li>            
                <Dropdown view={dropItemSet} content={menuDropItem_about} title={"關於我們"}/>
            </li>
            <li>
                <NavLink to="/news">
                    最新消息
                </NavLink>
            </li>
            <li>
                <Dropdown view={dropItemSet} content={menuDropItem_service} title={"解決方案"}/>
            </li>
            <li>
                <NavLink to="/project">
                    電廠實績
                </NavLink>
            </li>
            <li>
                <NavLink to="/contact">
                    聯絡我們
                </NavLink>
            </li>
            <li>
                <div className="header-rightline"></div>
                <Button href={"/"} type="link">
                    EN
                </Button>                
            </li>
        </ul>
    ];

    return (
        <>
            <header id="header" className={headerClassName + " " +  scroll + " " + dropclass} ref={ref}>
                <div className='header-bg'></div>
                <div className="header-container">
                    <Row justify="space-between">
                        <Col lg={8} md={6} sm={20} xs={20}>
                            <div className="header-logo">
                                <Link to="/" id="logo">
                                    <div className='logo-wrap'>
                                        <img src={logo} alt="logo"  className='logo'/>
                                    </div>
                                    <div className='logo-w-wrap'>
                                        <img src={logo_w} alt="logo" className='logo-w'/> 
                                    </div>                                                
                                </Link>
                            </div>                        
                        </Col>
                        <Col lg={16} md={18} sm={4} xs={4}>
                            <div
                                className={menuVisible ?'header-mobile-menu open':'header-mobile-menu'}
                                onClick={() => {
                                    phoneClick();
                                }}
                            >
                                <em />
                                <em />
                            </div>
                            { menu }
                        </Col>                    
                    </Row>
                    <div className="header-menuline"></div>   
                    <div className="header-bottomline"></div>                
                </div>   
            </header>
        </>
    );

})
export default Header;