import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import Header from "../component/Header";
import Footer from "../component/Footer";
import { Row, Col, BackTop, Breadcrumb, Skeleton } from 'antd';
import axios from 'axios';
import { token, news_api } from '../api/api.js';
import kvimg from "../images/news_bg.jpg";

export default function NewsPage(props) {    
    const id = useParams();
    const [fetchData,setFetchData] = useState(null);
    const filterData = ()=>{
        const currentData = fetchData.filter(item => {
            return item.vid === id.id;
        })
        const [currentItem] = currentData;
        return currentItem;
    }
    const rederData = ()=>{
        const currentItem = filterData();
        return <Row gutter={[64, 48]}>
                <Col lg={8} md={8} sm={24} xs={24}>
                    <div className='news-page-box-title'>                                   
                        <h1>{currentItem.title}</h1>                                    
                        <div className="publish">發布單位 ／ {currentItem.release_unit}</div>
                        <div className="date">{currentItem.dt.split("-")[0] + '.' + currentItem.dt.split("-")[1] + '.' + currentItem.dt.split("-")[2].slice(0,2)}</div>
                    </div>
                </Col>
                <Col lg={16} md={16} sm={24} xs={24}>
                    <div className="news-page-box-content">
                        <div dangerouslySetInnerHTML={{__html: currentItem.content} }></div>
                    </div>
                </Col>
            </Row>        
    }
    const idArray = ()=>{
        const arr =[]
        const filterArr = fetchData.filter(item =>{
            return item.external !== "Y";
        })
        filterArr.forEach((item,index)=>{
            arr.push(item.vid);
        });
        return arr;        
    }
    const prevPage = ()=>{
        const arr = idArray();        
        const index = arr.indexOf(id.id);
        if(index <= 0 ){
            return arr[arr.length-1];
        }else{
            return arr[index-1];
        }
    }  
    const nextPage = ()=>{
        const arr = idArray();
        const index = arr.indexOf(id.id);
        if(index >= arr.length-1 ){
            return arr[0];
        }else{
            return arr[index+1];
        }
    }
    const hotNews = () =>{
        const filterNews = fetchData.filter(item =>{
            return item.vid !== id.id;
        })
        return filterNews.slice(0,4);
    }
    //去除字串中的html標籤並只留下50字
    const delHtmlTag = (str) => {
        str = str.replace(/<[^>]+>/g,"");
        if(str.length > 50){
            str = str.substring(0,50);
        }
        return str;
    }
    const loadItem = () => {   
        let arr = [1,2,3,4];
        return arr.map((item,index) => {
            return <div className='news-item' key={index + '1'}>
                        <Skeleton active avatar />
                    </div>
        });
        
    };
    useEffect(()=>{
        axios({
            method: 'GET',
            url: news_api,
            responseType: 'json', 
            headers: {
                  Authorization: `Bearer ${token}` 
            }
        }).then(function (response) { // 成功後要做的事  
           setFetchData(response.data)         
        }).catch(function (error) { // 失敗後要做的事
            console.log(error);
        })
    },[]);
    return (
        <div>
            <Header/>
            {/* <div className='page-title'>
                <p className='page-title-sub'>最新消息</p>  
                <h1>NEWS</h1>
            </div>             
            <div className='breadcrumb'>
                <div className='container-s'>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            <Link to="/news">News</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            {fetchData ? filterData().title : ''}
                        </Breadcrumb.Item>
                    </Breadcrumb>
                </div>   
            </div> */}
            <div className='page-title-bg' style={{backgroundImage:`url(${kvimg})`}} id='pagetitle'>
                <p className='page-title-sub'>最新消息</p>  
                <h1>NEWS</h1>                
            </div>  
            <div className='breadcrumb'>
                <div className='container-s'>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            <Link to="/news">News</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            {fetchData ? filterData().title : ''}
                        </Breadcrumb.Item>
                    </Breadcrumb>
                </div>   
            </div>
            <section className='page-news-page-section'>
                <div className="container-s">
                    <div className="news-page-box">  
                        {fetchData ? rederData() : <Skeleton active avatar />} 
                    </div>
                </div>
            </section>
            <div className="news-page-next">
                <div className="container-s">
                    <Row justify="space-between" align="middle">
                        <Col lg={12} md={12} sm={12} xs={12}>
                            <Link className='news-page-next-back' to='/news'>Back to List</Link>
                        </Col>
                        <Col lg={12} md={12} sm={12} xs={12}>
                            <div className="pre-next">
                                <Link className="btn-more isview pre-btn" to={fetchData ? `/news/${prevPage()}`: ''}>
                                    <div className="btn-more-wrap">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
                                        <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                                        <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                                        </svg>
                                        <i></i>
                                    </div>
                                </Link>
                                <Link className="btn-more isview next-btn"  to={fetchData ? `/news/${nextPage()}` : ''}>
                                    <div className="btn-more-wrap">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 80 80">
                                        <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                                        <circle r="38" cx="40" cy="40" strokeWidth="2" fill="none"></circle>
                                        </svg>
                                        <i></i>
                                    </div>
                                </Link>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>
            <div className="news-page-hot">
                <div className="container-s">
                    <h3 className='news-page-hot-title'>HOT NEWS</h3>
                    <Row gutter={[48, 24]}>
                        {fetchData ? hotNews().map(item => {
                            return  <Col lg={12} md={12} sm={24} xs={24} key={item.vid}>
                                        <Link className='news-item' to={`/news/${item.vid}`} >
                                            <div className='news-item-date'>
                                                <div className='news-item-date-day'>{item.dt.split("-")[2].slice(0,2)}</div>
                                                <div className='news-item-date-month'>{item.dt.split("-")[0]+ "." + item.dt.split("-")[1]}</div>
                                            </div>
                                            <div className='news-item-content'>
                                                <h3>{item.title}</h3>
                                                <p>{delHtmlTag(item.content)}</p>
                                            </div>
                                        </Link>
                                    </Col>
                        }) : loadItem()}                     

                    </Row>
                </div>
            </div>
            <BackTop />
            <Footer/>
        </div>
        
    );
}