import React, { useEffect, useState } from 'react';
import useRWD from '../hook/useRWD';
import { Link } from 'react-router-dom';
import Header from "../component/Header";
import Footer from "../component/Footer";
import { BackTop, Breadcrumb, Image, Skeleton } from 'antd';
import { GoogleMap, Marker, useLoadScript } from '@react-google-maps/api';
import mapMarker from "../images/map-marker.svg";
import mapMarkerSelect from "../images/map-marker-select.svg";
import axios from 'axios';
import {token, location_api} from '../api/api.js';

const cityOrder = [
    '宜蘭縣',
    '基隆巿',
    '臺北巿',
    '新北巿',
    '桃園縣',
    '新竹縣',
    '新竹巿',
    '苗栗縣',
    '台中市',
    '彰化縣',
    '南投縣',
    '雲林縣',
    '嘉義縣',
    '嘉義巿',
    '台南市',
    '高雄市',
    '屏東縣',
    '臺東縣',
    '花蓮縣',
    '澎湖縣',
    '金門縣',
    '連江縣',
]; 

const containerStyle = {
    width: '100%',
    height: '100%'
};  
const center_hisesPC = {
    lat: 23.716359,
    lng: 122.5
}
const center_PC = {
    lat: 23.56359,
    lng: 123
};
const center_tablet = {
    lat: 23.56359,
    lng: 121.9
};
const center_mobile = {
    lat: 22.2,
    lng: 121.1
};
const defaultMapOptions = {
    fullscreenControl: false,
    mapId: "563988f8a27057bf",
    zoomControl: true,
    mapTypeControl: false,
    scaleControl: false,
    streetViewControl: false,
    rotateControl: false,
    panControl: true,
    maxZoom:10,
}; 

export default function Project() {    
    const { isLoaded } = useLoadScript({
        googleMapsApiKey: 'AIzaSyCBpM4Yg4uYrO6k_uDnZdlUXrxtS9xeQUc',
    });

    const device=useRWD();
    const [dataAjax,setDataAjax] = useState([]); 
    const [pvtitle, setPvtitle] = useState(""); 
    const [pvimgarry, setPvimgarry] = useState("");  
    useEffect(()=>{      
        axios({
            method: 'GET',
            url: location_api,
            responseType: 'json', 
            headers: {
                  Authorization: `Bearer ${token}` 
            }
        }).then(function (response) { // 成功後要做的事
            response.data.forEach(item => {
                if(item.location === '臺中巿'){
                    item.location = '台中市';
                }else if(item.location === '臺南巿'){
                    item.location = '台南市';
                }else if(item.location === '嘉義巿'){
                    item.location = '嘉義縣';
                }else if(item.location === '桃園市'){
                    item.location = '桃園縣';
                }else if(item.location === '高雄巿'){
                    item.location = '高雄市';
                }
            })          
            console.log(response.data);
            setDataAjax(response.data);          
        }).catch(function (error) { // 失敗後要做的事
            console.log(error);
        })

    },[]);

    const onMapLoad = (map) => {   
        axios({
            method: 'GET',
            url: location_api,
            responseType: 'json', 
            headers: {
                  Authorization: `Bearer ${token}` 
            }
        }).then(function (response) { // 成功後要做的事
            response.data.forEach(item => {
                if(item.location === '臺中巿'){
                    item.location = '台中市';
                }else if(item.location === '臺南巿'){
                    item.location = '台南市';
                }else if(item.location === '嘉義巿'){
                    item.location = '嘉義縣';
                }else if(item.location === '桃園市'){
                    item.location = '桃園縣';
                }else if(item.location === '高雄巿'){
                    item.location = '高雄市';
                }
            })
            const allArea = [];                      
            cityOrder.forEach( area =>{
                let filterArea = response.data.filter( item => {            
                    return item.location === area;
                })
                allArea.push(filterArea);
            });     
            setPvtitle(allArea[0][0].location);
            setPvimgarry(allArea[0]);
            map.data.loadGeoJson('twGeo.json');
            map.data.setStyle({
                strokeWeight: 1,
                strokeOpacity: 0,
                strokeColor: 'transparent',
                fillColor: 'transparent',
                fillOpacity: .3
            });
            map.data.addListener('click', function(event) {       
                allArea.forEach((item)=>{
                    if(item.length > 0){
                        if(item[0].location === event.feature.h.COUNTYNAME){   
                            map.data.revertStyle();
                            map.data.overrideStyle(event.feature, {fillColor: '#fddf9a'});
                            setPvtitle(event.feature.h.COUNTYNAME);  
                            setPvimgarry(item);                        
                        }
                    }  
                }) 
            });       
    
        }).catch(function (error) { // 失敗後要做的事
            console.log(error);
        })    
        
    }
    
    const loadMap = ()=>{
        return (
            <GoogleMap
                mapContainerStyle={containerStyle}                                             
                options={defaultMapOptions}
                center={device === 'hisesPC' ? center_hisesPC : device === 'PC' ? center_PC : device === 'tablet' ? center_tablet : center_mobile}
                zoom={device === 'hisesPC' ? 8 : device === 'PC' ? 8 : device === 'tablet' ? 8 : 6.7}
                onLoad={onMapLoad}
            >
               {dataAjax.map( item => {    
                    if(item.location === pvtitle){
                        return <Marker position={{lat:item.latt, lng:item.longt}} icon={mapMarkerSelect} key={item.vid} clickable={false}/>
                    }else{
                        return <Marker position={{lat:item.latt, lng:item.longt}} icon={mapMarker} key={item.vid} clickable={false}/>
                    }                     
                                                
                })}
                <></>
            </GoogleMap>
        )
    }

    return (
        <div>
            <Header/>
            <div className='page-title'>
                <p className='page-title-sub'>電廠實績</p>  
                <h1>PROJECT</h1>
            </div>           
            <div className='breadcrumb'>
                <div className='container'>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            Project
                        </Breadcrumb.Item>
                    </Breadcrumb>
                </div>   
            </div>
            <div className='project-map'>   
                { isLoaded ? loadMap() : <div>loading...</div> }
                <div className='project-map-wrap'>
                    <div className='project-map-count'>
                        全台案場: <span>240</span>
                    </div>
                    <div className='project-map-panel'>
                        <div className='project-map-panel-header'>
                            <h2>{pvtitle ? pvtitle : <div className='loading-skeleton'></div>}</h2>
                            <div className='project-map-area-count'>共 <span>{pvimgarry.length !== 0 ? pvimgarry.length : 0} 案場</span></div>
                        </div>
                        <div className='project-map-panel-content'>
                            <div className="project-map-panel-scroller">
                                {pvimgarry.length !== 0 ? pvimgarry.map(item =>{
                                    return <Image
                                                width={'100%'}
                                                src={`https://www.rfdme.com/images/works/big/img_${item.pno.toLowerCase()}_map.jpg`}
                                                alt={item.pno.toLowerCase()}
                                                key={item.vid}
                                            />
                                }): <Skeleton active />}     
                            </div>                                                  
                        </div>
                    </div> 
                </div>
            </div>
            <div className="project-photos">
                <div className='project-photos-container'>
                    {dataAjax ? dataAjax.map(item=>{
                       return   <div className='project-photos-wrap' key={item.pno}>
                                    <Image width={'100%'} src={`https://www.rfdme.com/images/works/big/img_${item.pno.toLowerCase()}_map.jpg`} alt={'project'} />
                                    <div className='project-photos-mask'></div>
                                    <div className='project-photos-text'>
                                        <h4>{item.location}</h4>
                                        <p>{item.name}</p>
                                    </div>  
                                </div> 
                    }) : <Skeleton.Image active /> }

                </div>
            </div>  
            <BackTop />
            <Footer/>
        </div>
        
    );
}
